}

:root {
  --main-font: "Roboto";
  --main-cl: #2c2c2c;
  --black: #000;
  --btn-gr: #bbf330;
  --btn-gr-hov: #b5ff00;
  --border-btn: #7eb101;
  --border-btn-hov: #597d00;
  --yellow-main: #ffe550;
  --white: #fefefe;
  --bg-cl: #f5f5f5;
  --indent: 32px;
  --items: 1;
  --trans-fast: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--main-font), sans-serif;
  color: var(--main-cl);
  background-color: var(--bg-cl);
  font-size: 16px;
  line-height: 1.5;
}

body.no-scroll {
  overflow: hidden;
}

h1, h2, h3, h4, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.card-set {
  gap: var(--indent);
  flex-wrap: wrap;
  display: flex;
}

.card-set__item {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

